import * as React from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import BasicModal from "./BasicModal";
import ActionAreaCard from "./ActionAreaCard";
import kouka from "./kouka";
import name from "./name";

function images(index) {
  const items1 = [];
  const keys1 = Object.keys(image[index]);
  const values1 = Object.values(image[index]);
  for (let i = 0; i < values1.length; i++) {
    items1.push(<img src={values1[i]}></img>);
  }
  return <ul>{items1}</ul>;
}

function names(index) {
  const items2 = [];
  const keys2 = Object.keys(name[index]);
  const values2 = Object.values(name[index]);
  for (let i = 0; i < values2.length; i++) {
    items2.push(<h3>{values2[i]}</h3>);
  }
  return <ul>{items2}</ul>;
}

function effect(index) {
  const items3 = [];
  const keys3 = Object.keys(kouka[index]);
  const values3 = Object.values(kouka[index]);
  for (let i = 0; i < values3.length; i++) {
    items3.push(
      <h5>
        {keys3[i]}:{values3[i]}
      </h5>
    );
  }
  return <ul>{items3}</ul>;
}

